#editButton {

    display: inline-block;
    outline: 0;
    cursor: pointer;
    border-radius: 6px;
    border: 1px solid #07b650;
    color: #fff;
    background-color: #07b650;
    padding: 8px;
    box-shadow: rgba(0, 0, 0, 0.07) 0px 2px 4px 0px, rgba(0, 0, 0, 0.05) 0px 1px 1.5px 0px;
    font-weight: 800;
    font-size: 16px;
    height: 42px;      
}

#editButton:hover{
    background: 0 0;
    color: #07b650;
}

#deleteButton {

    display: inline-block;
    outline: 0;
    cursor: pointer;
    border-radius: 6px;
    border: 2px solid #ff4742;
    background-color: #ff4742;
    color: #fff;
    
    padding: 8px;
    box-shadow: rgba(0, 0, 0, 0.07) 0px 2px 4px 0px, rgba(0, 0, 0, 0.05) 0px 1px 1.5px 0px;
    font-weight: 800;
    font-size: 16px;
    height: 42px;      
}

#deleteButton:hover{
    background: 0 0;
    color: #ff4742;
   

}

